import React, { Suspense, lazy } from 'react';
//router imports
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
//sass imports
import '../src/Sass/main.scss';
//component imports
import NavBar from './Components/UiComponents/Navigation/NavBar';
import ScrollTop from './Components/Functions/ScrollTop';
//import in the form of variables for lazy load react
const Home = lazy(() => import('./Components/PageComponets/Home'));
const Login = lazy(() => import('./Components/PageComponets/Login'));
const ProfilePage = lazy(() =>
  import('./Components/PageComponets/ProfilePage')
);
const Signup = lazy(() => import('./Components/PageComponets/Signup'));
const Error = lazy(() => import('./Components/PageComponets/Error'));
const AboutDevelopers = lazy(() =>
  import('./Components/PageComponets/AboutDevelopers')
);
const AboutGinnovation = lazy(() =>
  import('./Components/PageComponets/AboutGinnovation')
);
const AboutAlumni = lazy(() =>
  import('./Components/PageComponets/AboutAlumni')
);
const ForgetPassword = lazy(() =>
  import('./Components/PageComponets/ForgetPassword')
);
const ResetPassword = lazy(() =>
  import('./Components/PageComponets/ResetPassword')
);
const BlogDetails = lazy(() =>
  import('./Components/PageComponets/BlogDetails')
);
const AllBlogs = lazy(() => import('./Components/PageComponets/AllBlogs'));
const AllProjects = lazy(() =>
  import('./Components/PageComponets/AllProjects')
);
//components import
