import React, { useEffect, useState } from 'react';
import { Avatar } from 'antd';
import {
  LoginOutlined,
  HomeOutlined,
  BulbOutlined,
  ApiOutlined,
  InfoCircleOutlined,
  CloseOutlined,
  PicCenterOutlined,
  CaretUpOutlined,
  CaretDownOutlined,
} from '@ant-design/icons';
//router imports
import { NavLink } from 'react-router-dom';
//assets import
import logo from '../../../assets/logo.png';
//redux
import { useSelector } from 'react-redux';
//--------------------------------------------------------------
const NavBar = () => {
  const [expand, setexpand] = useState(false);
  const [drop, setdrop] = useState(false);
  const [userDisplayName, setUserDisplayName] = useState('');
  const [userIcon, setuserIcon] = useState('');
  const userFullNamee = useSelector((state) => state.userNameReducer.userName);
  // const userFullNamee = localStorage.getItem('username');

  useEffect(() => {
    const userFullName = userFullNamee;
    if (userFullName !== null) {
      const userName = userFullName.split(' ')[0];
      setUserDisplayName(userName);
      const icon = userName.charAt(0);
      setuserIcon(icon);
    }
  }, [userFullNamee]);
  const auth = useSelector((state) => state.loginReducer.isAuth);

  return (
    <nav className='nav-bar'>
      <h1 className='navbar-logo'>
        <NavLink to='/'>
          <img src={logo} alt='ginnovation logo' />
        </NavLink>
      </h1>
      <div
        className='menu-icon'
        onClick={() => {
          setexpand(!expand);
        }}
      >
        {expand ? (
          <CloseOutlined className='icon' />
        ) : (
          <PicCenterOutlined className='icon' />
        )}
      </div>
      <ul className={expand ? 'item-container active' : 'item-container'}>
        <li>
          <NavLink to='/' className='nav-items'>
            <HomeOutlined className='icon' /> Home
          </NavLink>
        </li>
        <li>
          <NavLink to='/blogs' className='nav-items'>
            <BulbOutlined className='icon' /> Blogs
          </NavLink>
        </li>
        <li>
          <NavLink to='/projects' className='nav-items'>
            <ApiOutlined className='icon' /> Projects
          </NavLink>
        </li>
        <li>
          <span
            className='nav-items'
            onClick={() => {
              setdrop(!drop);
            }}
          >
            <InfoCircleOutlined className='icon' /> About
            {drop ? (
              <CaretUpOutlined className='icon' />
            ) : (
              <CaretDownOutlined className='icon' />
            )}
            {drop && (
              <ul className='drop-down'>
                <li>
                  <NavLink to='/about/alumni' className='navlink'>
                    About Alumni
                  </NavLink>
                </li>
                <li>
                  <NavLink to='/about/developers' className='navlink'>
                    About Developers
                  </NavLink>
                </li>
                <li>
                  <NavLink to='/about' className='navlink'>
                    About Ginnovation
                  </NavLink>
                </li>
              </ul>
            )}
          </span>
        </li>
        {auth && (
          <li>
            <NavLink to='/profile' className='nav-items'>
              <Avatar
                className='icon avatar'
                style={{
                  color: '#fff',
                  background: 'linear-gradient(to right, #373b44, #4286f4)',
                }}
              >
                {userIcon}
              </Avatar>
              {userDisplayName}
            </NavLink>
          </li>
        )}
        {auth ? (
          ''
        ) : (
          <li>
            <NavLink to='/login' className='nav-items'>
              <LoginOutlined className='icon' /> Login/Signup
            </NavLink>
          </li>
        )}
      </ul>
    </nav>
  );
};

export default NavBar;
